export default {
	data() {
		return {
			detail:[],
		}
	},
	onLoad(e) {
		var t=this;	
		uni.showLoading();
		t.urlRequest.urlRequest(t.func.getActivityInfo,{partyday_log_id:e.id},function(res){
			t.detail=res.data.status_list
		})
		uni.hideLoading();
	},
	methods: {
		activeDetail:function(id){
			uni.navigateTo({
				url: '../index1/active-detail?id='+id
			})
		},
	}
}
